import { customElement, property } from 'lit/decorators.js';
import { css, CSSResultGroup, html, LitElement, TemplateResult } from 'lit';
import './zwc-avatar';

@customElement('zwc-avatar-list')
export class ZwcAvatarList extends LitElement {
  @property({ type: String, attribute: 'background-color', reflect: true })
  backgroundColor = '';

  @property({ type: String })
  size = 'md';

  @property({ type: Array })
  avatars = [];

  static override styles?: CSSResultGroup | undefined = [
    css`
      .avatar-list-sm :not(:first-child) {
        margin-left: -10px;
      }
      .avatar-list-md :not(:first-child) {
        margin-left: -15px;
      }
      .avatar-list-lg :not(:first-child) {
        margin-left: -15px;
      }
      .avatar-list-sm ::slotted(:not(:first-child)) {
        margin-left: -10px;
      }
      .avatar-list-md ::slotted(:not(:first-child)) {
        margin-left: -15px;
      }
      .avatar-list-lg ::slotted(:not(:first-child)) {
        margin-left: -15px;
      }
    `,
  ];

  protected override render(): TemplateResult {
    return html`
      <div class="avatar-list-${this.size}">
        ${this.avatars.length > 0
          ? this.avatars.map(
              (avatar: any) =>
                html`<zwc-avatar
                  size="${this.size}"
                  background-color="${avatar.backgroundColor}"
                >
                  ${avatar.text}
                </zwc-avatar>`,
            )
          : html`<slot></slot>`}
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-avatar-list': ZwcAvatarList;
  }
}
